import PlantAgent from "../sensor/PlantAgent";

export default function SensorPrinter() {

    const humidityReceived = (ev) => {
        console.log(ev.target.value.getUint8(0))
        console.log("hello")
    }


    function disconnectSensor() {
        PlantAgent.disconnect()
    }

    function requestSensor() {
        PlantAgent.request();
    }

    function connectSensor() {
        PlantAgent.connect()
    }

    function startSensor() {
        PlantAgent.startHumidityNotifications(humidityReceived)
    }

    return (
        <div>
            <button
            onClick={() => requestSensor()}>Request</button>
            <button
                onClick={() => connectSensor()}>Connect</button>
            <button
                onClick={() => startSensor()}>Start notifications</button>
            <button
                onClick={() => disconnectSensor()}>
                Disconnect
            </button>

        </div>
    );
}
